.comments{
	display: none;
}
.comments.active{
	position: relative;
	z-index: 1;
	border-top: 4px solid hsl(223, 19%, 93%);
	margin: 20px;
	display: flex;
	height: auto;
	flex-direction: column;
	animation-name: slide, opacitier, blockheigth;
	animation-duration: 1s, 1s, 1s;
	animation-timing-function: linear, linear, linear;
}

@keyframes slide {
	0%{	
		transform: translateY(-300px);
	}
	100%{
		transform: translateY(0px);
	}
}
@keyframes opacitier {
	0%{	
		opacity:0;
	}
	50%{
		opacity:0;
	}
	100%{
		opacity:1;
	}
}
@keyframes blockheigth {
	0%{	
		height: 0;
	}
	10%{
		height: 40px;
	}
	20%{
		height: 80px;
	}
	30%{
		height: 120px;
	}
	40%{
		height: 160px;
	}
	50%{	
		height: 200px;
	}
	60%{
		height: 240px;
	}
	70%{
		height: 280px;
	}
	80%{
		height: 320px;
	}
	90%{
		height: 360px;
	}
	100%{
		height: auto;
	}
}