.modal{
	position:fixed;
	top:0;
	bottom: 0;
	right: 0;
	left: 0;
	display: none;
	background-color: rgba(234, 225, 246, 0.37);
}

.content{

	padding: 25px;
	background-color: white;
	border-radius: 16px;
	max-width: 800px;
	max-height: 500px;
	img{
		max-width: 800px;
		max-height: 500px;
	}
}

.modal.active{
	
	display: flex;
	justify-content: center;
	align-content: center;
	justify-content: center;
	align-items: center;
	z-index: 3;
}
.content__body{
	position: relative;
	box-sizing: border-box;
	max-height: 100%;
	max-width: 100%;
	width: 1;
}
.prev,.next{
	box-sizing: content-box;
	height: 100%;
	width: 30%;
	z-index: 4;
	position: absolute;
	background-color: rgba(black, 0.0001);
	padding: 0px;
}
.prev:hover,.next:hover{
	background-color: rgba(black, 0.2);
}
.prev{
	left: 0px;

}
.next{
	right: 0px;
}

@media (max-width:767px) {
	.content{

		padding: 25px;
		background-color: white;
		border-radius: 16px;
		max-width: 100%;
		max-height: 100%;
		img{
			max-width: 100%;
			max-height: 100%;
		}
	}
}