.body{
	flex:10%;
	height: 50px;
	display: flex;
	text-align: center;
	justify-content: space-between;
	width: 60%;
	align-items: center;
}

.comments, .like, .dislike{
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	background-color: rgb(240, 235, 243);
	transition: background-color 2s ease;
	padding: 2px 7px;
	border-radius: 10px;

	align-items: center;
	img{
		margin-right: 5px;
		max-height: 30px;
	}
}
.like{
 img{
	 margin-bottom: 5px;
 }
}
.dislike{
	img{
		margin-top: 5px;
	}
}

.comments{
	padding: 5px 10px;
	img{
		max-height: 30px;
		margin-right: 8px;
	}
}

.like.active, .dislike.active{
	background-color: rgb(230, 189, 254);
	animation: liked 1.5s linear;
	img{	
	}
}
@keyframes liked {
	0%{	
		transform: scale(1);
	}
	70%{
		transform: scale(1.3);
	}
	100%{
		transform: scale(1);
	}
}