.uptools{
	display: flex;
	justify-content: space-between;
	margin-bottom: 10px;
}
.left-part{
	flex: 0 1 90%;
	display: flex;
	flex-direction: row;
}
.avatar{
	img{
		max-height: 50px;
		position: relative;
		object-fit: scale-down;
	}
}
.dateName{
	text-align: left;
	padding: 6px;
	margin-left: 20px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
}
.settings{
	align-self: right;
	display: flex;
	justify-content: center;
	align-content: center;
	img{
		max-width:40px;
		max-height: 40px;

	}

}