.modal{
	padding: 50px 0px 20px 20px;
	position:absolute;
	display: none;
	z-index: 3;
	top: 10px;
	right: 20px;
	
}

.content{
	position:sticky;
	min-width: 150px;
	max-width: 250px;
	box-shadow: 10px 10px 10px 0px rgba(34, 60, 80, 0.2),
	-10px 10px 10px 0px rgba(34, 60, 80, 0.2),
	10px -10px 20px 0px rgba(34, 60, 80, 0.2),
	-10px -10px 20px 0px rgba(34, 60, 80, 0.2);
	background-color: white;
	padding: 20px;
	border-radius: 15px;

}
.modal.active{
	display: flex;
}
button{
	background-color: #fff;
	border: white;
	padding: 10px 20px;
}