.body{
	box-sizing: content-box;
	height: 50px;
	max-width: 100%;
	background-color: #fff;
	border-radius: 15px;
	padding: 10px;
	margin: 10px;
	display:flex;
	justify-content: space-between;
	align-items: center;
}
.image{
	max-width: 30px;
	max-height: 30px;
}
.text{
	flex: 0 1 70%;
	outline:none;
	border: 0;
	height: 90%;
	width: auto;
	font-size: 18px;
	resize:none
}

.file{
	opacity: 0;
	visibility: hidden;
	position: absolute;
}


.btn{
	color: black;
	border-radius: 10px;
	background-color: rgba(151, 81, 151, 0.148);
}
.body.active{
	height: 100px;
	display: grid;
	grid-template: 3fr 0.5fr/0.2fr 1fr 1fr;
	grid-template-areas: "ava text text"
						 "emoji attach send";
						 
	row-gap: 3px;
	column-gap: 3px;
	.image{
		grid-area: ava;
	}
	.input__file_button{
		grid-area: attach;
		margin: auto;
	}
	.text{
		grid-area: text;
	}
	.btn{
		grid-area: send;
	}
	.emoji{
		grid-area: emoji;
	}
}
